<template>
    <div class = "ui centered card" >
          <div class="content" v-show="!isEditing">
              <div class = "header">
                  {{todo.title}}
              </div>
              <div class = "meta">
                  {{todo.project}}
              </div>
              <div class = "extra content">
                  <span class = "right floated edit icon" v-on:click="showForm">
                      <i class = "edit icon"></i>
                  </span>
                  <span class='right floated trash icon' v-on:click="deleteTodo(todo)">
                     <i class='trash icon'></i>
                  </span>
              </div>
          </div>
          <!--editting form-->
          <div class="content" v-show="isEditing">
            <div class='ui form'>
                <div class='field'>
                  <label>Title</label>
                  <input type='text' v-model="todo.title" >
             </div>
             <div class='field'>
                 <label>Project</label>
                  <input type='text' v-model="todo.project" >
             </div>
             <div class='field'>
                 <label>Done</label>
                  <!--input type='checkbox' v-model="todo.done" -->
             </div>
                <div class='ui two button attached buttons'>
                    <button class='ui basic blue button' v-on:click="hideForm">
                         Close X
                     </button>
                </div>
            </div>
            </div>
          <button class ="ui basic green basic button" v-on:click="toggleDone(todo)" v-if="todo.done">
              completed
          </button>
          <button class ="ui basic red basic button" v-on:click="toggleDone(todo)" v-else>
              Incompleted
          </button>
          
      </div>
</template>

<script>
export default {
    props:["todo"],
    data(){
        return{
            isEditing:false
        };
    },
    methods:{
        showForm(){
            this.isEditing = true;
        },
        hideForm(){
            this.isEditing = false;
        },
        deleteTodo(todo){
            this.$emit("delete-todo", todo);
        },
        toggleDone(todo){
           if(!todo.done){
               alert("Congratulations, you've completed an item!");
           }
           todo.done = !todo.done;
        }
    }
};
</script>
    
<style>
.green{
        color: green;
        border:1px green solid;
    }
</style>



